:root {
  --color-select: #1f6feb;
  --color-select-hover: #5497ff;
  --color-remove: #ff003d;
  --color-select-text: #ffffff;
  --color-select-text-hover: #ffffff;
  --color-free: #24ff00;
  --color-bg-1: #14151c;
  --color-bg-0: #0d0e15;
  --color-bg-0-lighter: #222434;
  --color-bg-2: #1b1c26;
  --color-bg-3: #20212b;
  --color-bg-4: #2e2f3c;
  --color-text-0: #ffffff;
  --color-text-1: rgba(255, 255, 255, 0.6);
  --color-text-2: rgba(255, 255, 255, 0.4);
  --icons: url(images/icons.svg);
  --shadow-app-hover: 0px 25px 25px 16px black;
  --image-app-hover-arrow: url(images/app_hover_arrow.svg);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font: 16px / 24px "Segoe UI", "San Francisco", -apple-system,
    BlinkMacSystemFont, ".SFNSText-Regular", "Arial", "Helvetica", sans-serif;
  background-color: var(--color-bg-1);
  color: var(--color-text-1);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.stars {
  display: flex;
  align-items: center;
  margin-right: 4px;
}
.star {
  background-image: var(--icons);
  background-repeat: no-repeat;
  background-position: -39px -72px;
  width: 12px;
  height: 12px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;
  margin-right: 1px;
}
.star.empty {
  background-position: -39px -60px;
}
.app_rating {
  display: flex;
  align-items: center;
}

.app_tags {
  text-overflow: hidden;
}
.app_tag {
  text-transform: capitalize;
  margin-right: 10px;
  text-overflow: hidden;
}

.app_tag:last-child {
  margin-right: 0;
}

body {
  counter-set: appCounter;
}

.app_info {
  display: flex;
  flex-direction: column;
  padding: 16px;
  width: 100%;
}

.app_info_right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app_info_right > div {
  margin-right: 16px;
}
.app_info_right > div:last-child {
  margin-right: 0;
}

.app-price.free {
  color: var(--color-free);
}

a.app {
  text-decoration: none;
  color: inherit;
}

a {
  background: var(--color-bg-0);
}

a:hover {
  background: var(--color-bg-0-lighter);
}

.applist_parent {
  padding-top: 40px;
  width: calc(1040px + 300px);
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.applist_parent {
  position: relative;
  display: flex;
  align-items: flex-start;
}

.results_size {
  font-size: 16px;
  margin-bottom: 24px;
}

.results_size_number {
  font-size: 40px;
}

#reset_filters {
  width: 100%;
  border-radius: 8px;
  border-radius: 4px;
  margin-bottom: 14px;
  background: var(--color-select);
  padding-bottom: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-select-text);
  cursor: pointer;
}

#reset_filters.hidden {
  display: none;
}

.image_container {
  width: 100%;
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
  overflow: hidden;
}

.image_container .hover_zones {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
.image_container .hover_zones > div {
  width: 25%;
  height: 100%;
  float: left;

  z-index: 2;
  cursor: pointer;
}
